.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


input[type='text'] {
  line-height: 40px;
  float: left;
  width: 200px;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 5px;
  border: 1px #ccc solid;
  outline: none;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
  margin-left: 40px;

}

input[type='button'] {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
  height: 40px;
  width: 110px;
  font-size: 18px;
  color: #fff;
  border-radius: 5px;
  float: left;
  margin-left: 20px;
  margin-right: 50px;
}

.todoList{
  margin-left: 40px;
  margin-top: 80px;
  background-color: white;
}




@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



